<template>
    <div class="cart">
        <v-container pa-2>
            <v-layout align-start justify-space-between row wrap>
                <v-flex xs8>
                    <v-layout column>
                        <v-flex>
                            <v-layout column>
                                <v-flex>
                                </v-flex>
                                <v-flex>
                                </v-flex>
                            </v-layout>
                        </v-flex>
                        <v-flex>
                            <div class="cart__control">
                                <div class="header">
                                    <v-icon>mdi-alert-circle</v-icon>
                                    <p class="body-1">Ваш товар будет отправлен в день заказа</p>
                                </div>
                                <div
                                  v-for="(item, index) in items"
                                  :key="index"
                                  class="items"
                                >
                                    <v-layout justify-space-between row>
                                        <v-flex>
                                          <!-- <img :src="`../static/${item.img}`" :alt="`Так выглядят ${item.img}`"> -->
                                        </v-flex>
                                        <v-flex>
                                          <v-layout>
                                            <v-flex>
                                              <p class="text-truncate">
                                                {{ item.name }}
                                              </p>
                                            </v-flex>
                                          </v-layout>
                                        </v-flex>
                                    </v-layout>
                                </div>
                            </div>
                        </v-flex>
                    </v-layout>
                </v-flex>
                <v-flex v-if="getTotal" xs4>
                    <!-- <v-layout align-start column> -->
                        <!-- <v-flex> -->
                            <div class="cart__order">
                                <h2 class="font-weight-regular display-1">Сумма заказа</h2>
                                <ul class="cart__order-list">
                                    <li>
                                        <span>Цена</span>
                                        <span>{{ getTotalPrice }} <v-icon>mdi-currency-rub</v-icon></span>
                                    </li>
                                    <li>
                                        <span>Доставка</span>
                                        <span>Бесплатно</span>
                                    </li>
                                    <li>
                                        <b>К оплате</b>
                                        <b>
                                          {{ getTotalPrice }}
                                          <v-icon>mdi-currency-rub</v-icon>
                                        </b>
                                    </li>
                                </ul>
                                <v-btn large to="/payment">Оформить заказ</v-btn>
                            </div>
                        <!-- </v-flex> -->
                      <!-- <v-flex>
                        <div class="cart__promo">
                          <v-text-field
                          label="Введите промо-код"
                          ></v-text-field>
                          <v-btn>Применить</v-btn>
                        </div>
                      </v-flex> -->
                    <!-- </v-layout> -->
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
// import Product from '@/components/Product'
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      items: 'getStock',
      getTotal: 'getTotal',
      getTotalPrice: 'getTotalPrice'
    })
  },
  methods: {
    ...mapActions([
      'addItem',
      'removeItem',
      'clearCart'
    ])
  }
}
</script>

<style lang="scss" scoped>
</style>



